<template>
  <div class="page_main" ref="cavsContainer">
    <canvas ref="cavs" width="700" height="1400"></canvas>
    <aside class="btn" @click="doClick">add</aside>
    <div class="input_wrap">
      <input type="text" v-model="inputTxt" />
      <aside class="inputBtn" @click="doSend">send</aside>
    </div>
  </div>
</template>

<script>
// import {getRandomNum} from "../../utils/common";
import Danmu from "./danmu";
export default {
  data() {
    return {
      inputTxt: ""
    };
  },
  mounted() {
    this.canvas = this.$refs.cavs;
    let ctx = this.canvas.getContext("2d");
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;
    ctx.font = "20px Arial";
    ctx.textAlign = "start";
    ctx.fillStyle = "#ffff00";
    this.ctx = ctx;
    Danmu.initDamuList();
    this.loop();
  },
  methods: {
    loop() {
      try {
        requestAnimationFrame(this.loop);
        this.draw();
      } catch (error) {
        console.log("[log]==>", error);
      }
    },
    draw() {
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

      let danmuList = Danmu.prototype.danmuList;
      let canRenderNum = 0;

      // console.time("xxx");
      for (let i = 0; i < danmuList.length; i++) {
        for (let j = 0; j < danmuList[i].length; j++) {
          let curDanmu = danmuList[i][j];
          curDanmu.update();
          //
          if (curDanmu.isDead) {
            continue;
          }
          canRenderNum++;
          let {txt, x, y} = curDanmu;
          this.ctx.fillText(txt, x, y);
        }
      }
    },
    doClick() {
      var txt = "WTF u r talking about?";
      Danmu.add({ctx: this.ctx, txt});
    },
    doSend() {
      this.inputTxt && Danmu.add({ctx: this.ctx, txt: this.inputTxt});
      this.inputTxt = "";
    }
  }
};
</script>

