import { getRandomNum } from "../../utils/common";

const GAP = 20;
const STARTX = window.innerWidth / 2;
let DANMU_LIST_INFO = [];
const DANMU_COL_NUM = 5;
for (let n = 0; n < DANMU_COL_NUM; n++) {
  let obj = {};
  obj.speed = getRandomNum(2, 5);
  // obj.speed = 1;
  DANMU_LIST_INFO.push(obj);
  console.log(DANMU_LIST_INFO);
}

class Danmu {
  constructor({ txt, x, y, txtWidth, speed }) {
    this.txt = txt;
    this.x = x;
    this.y = y;
    this.speed = speed;
    this.txtWidth = txtWidth;
    this.a = 0.02;
    this.isDead = false;
  }

  update() {
    // this.speed += this.a;
    this.x = this.x - this.speed;
    if (this.x + this.txtWidth < 0) {
      this.isDead = true;
    }
  }

  static calTxtWidth({ ctx, txt }) {
    return ctx.measureText(txt).width;
  }

  static add = ({ ctx, txt }) => {
    let childInfo = {};
    let txtWidth = this.calTxtWidth({ ctx, txt });
    let curX = STARTX;

    let danmuList = this.prototype.danmuList;
    let minXIndex = 0;

    for (let i = 0; i < danmuList.length; i++) {
      if (danmuList[i].length === 0) {
        minXIndex = i;
        break;
      } else {
        if (danmuList[i][danmuList[i].length - 1].txtWidth + danmuList[i][danmuList[i].length - 1].x < danmuList[minXIndex][danmuList[minXIndex].length - 1].txtWidth + danmuList[minXIndex][danmuList[minXIndex].length - 1].x) {
          minXIndex = i;
        }
      }
    }

    let lastTxt = danmuList[minXIndex][danmuList[minXIndex].length - 1];
    lastTxt && (curX = lastTxt.x + lastTxt.txtWidth + GAP);

    if (curX < window.innerWidth) {
      curX = window.innerWidth;
    }
    // curX < STARTX ? STARTX : curX,

    childInfo = {
      speed: DANMU_LIST_INFO[minXIndex].speed,
      txt,
      x: curX,
      y: 50 * minXIndex + 50,
      txtWidth
    };

    let danmuChild = new Danmu(childInfo);
    this.prototype.danmuList[minXIndex].push(danmuChild);
  };

  static initDamuList() {
    for (let i = 0; i < DANMU_LIST_INFO.length; i++) {
      this.prototype.danmuList.push([]);
    }
  }
}

Danmu.prototype.danmuList = [];

export default Danmu;
